.nav {

    position:fixed;
    top:0;
    width : 100%;
    display: flex;
    padding:20px;
    height:30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__black {
    background-color: #111;
}
.nav_logo {
    width: 80px;
    left:20px;
    position: fixed;
    object-fit: contain;
}

.nav__avatar {

    position:fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}

